<template>
	<view style="background-color: white;">
		<view>
			<u-navbar :is-back="true" back-icon-color="white" back-text="返回" :back-text-style="{ color: 'white' }" title="心灵鸡汤" :background="bgColor" title-color="white" title-size="40" ></u-navbar>
		</view>
		<!-- 模态框 -->
		<view>
			<u-modal v-model="show" @confirm="confirm" :content="content" show-cancel-button="true"></u-modal>
		</view>
		
		<!-- 标题 -->
		<view class="top">{{title}}</view>
		
		<!-- 文章内容图文混排 -->
		<view v-for="(item,index) in list" style="margin-left: 25rpx;margin-right: 23rpx; margin-top: 10rpx;">
			<view v-if="item.text === null">
				<view v-if="item.img === null">
					
				</view>
				<view v-else>
					<img :src="item.img">
				</view>
			</view>
			<view v-else>
				<view v-if="item.img === null">
					<p>{{item.text}}</p>
				</view>
				<view v-else>
					<p>{{item.text}}</p>
					<img :src="item.img">
				</view>
			</view>
		</view>
		
		<!-- 发布时间 -->
		<view class="bottom_left">
			<p>阅读 {{readNumber}}</p>
		</view>
		
		<view class="bottom_right">
			<p>{{date}}</p>
		</view>
		
		<!-- 点赞 -->
		<view class="up">
			<view class="left" @click="detailsUp()">
				<u-icon :name="up" :color="upColor" size="50"></u-icon>
				<view>{{upCount}}</view>
			</view>
			<view class="right" @click="detailsDown()">
				<u-icon :name="down" :color="downColor" size="50"></u-icon>
				<view>{{downCount}}</view>
			</view>
		</view>
		
		<!-- 评论 -->
		<u-tabs :list="tabsList" :is-scroll="false" :current="current" @change="change"></u-tabs>
		<view style="border-bottom: 2px solid #1E90FF; margin-left: 25rpx; margin-right: 400rpx; margin-top: 25rpx;"></view>
		
		<view v-if="current == 0">
			<!-- 热门评论模板 -->
			<view v-for="(item,index) in commentList0">
				<view class="details" v-show="item.delShow">
					<view class="details_left">
						<view class="avatar">
							<u-avatar :src="item.avatarSrc" mode="square" size="mini"></u-avatar>
						</view>
					</view>
					<view class="details_right">
						<view class="details_right_top">
							<span>{{item.nickname}}</span><br>
							<span>{{item.commentDate}}</span>
						</view>
						<view class="del" v-show="item.commentShow" @click="delcomment(index, item.pid)">删除</view>
						<p>{{item.commentText}}</p>
						<view class="details_right_bottom">
							<view class="details_right_bottom_left" @click="reply(item.pid)">{{item.reCount}}回复 ></view>
							<view class="details_right_bottom_right">
								<view class="details_right_bottom_right_up" @click="commentUpFun(index, item.pid)">
									<u-icon :name="item.commentUp" :color="item.commentUpColor" size="40"></u-icon>
									<view>{{item.commentUpCount}}</view>
								</view>
								<view class="details_right_bottom_right_down" @click="commentDownFun(index, item.pid)">
									<u-icon :name="item.commentDown" :color="item.commentDownColor" size="40"></u-icon>
									<view>{{item.commentDownCount}}</view>
								</view>
							</view>
						</view>
					</view>
				</view>
				<view v-if="index % 2 ==0">
					<view style="border-bottom: 2px solid #BA55D3; margin-right: 25rpx; margin-left: 400rpx;"></view>
				</view>
				<view v-else>
					<view style="border-bottom: 2px solid #1E90FF; margin-right: 25rpx; margin-left: 400rpx;"></view>
				</view>
			</view>
			
			<!-- 加载更多-->
			<u-loadmore :status="status" :icon-type="iconType" :load-text="loadText" font-size = 29 margin-top = 15 margin-bottom = 110 color="#1E90FF" icon-color="#1e90ff"  />
		</view>
		
		<!-- 最新评论模板 -->
		<view v-if="current == 1">
			<view v-for="(item,index) in commentList1">
				<view class="details" v-show="item.delShow">
					<view class="details_left">
						<view class="avatar">
							<u-avatar :src="item.avatarSrc" mode="square" size="mini"></u-avatar>
						</view>
					</view>
					<view class="details_right">
						<view class="details_right_top">
							<span>{{item.nickname}}</span><br>
							<span>{{item.commentDate}}</span>
						</view>
						<view class="del" v-show="item.commentShow" @click="delcomment(index, item.pid)">删除</view>
						<p>{{item.commentText}}</p>
						<view class="details_right_bottom">
							<view class="details_right_bottom_left" @click="reply(item.pid)">{{item.reCount}}回复 ></view>
							<view class="details_right_bottom_right">
								<view class="details_right_bottom_right_up" @click="commentUpFun(index, item.pid)">
									<u-icon :name="item.commentUp" :color="item.commentUpColor" size="40"></u-icon>
									<view>{{item.commentUpCount}}</view>
								</view>
								<view class="details_right_bottom_right_down" @click="commentDownFun(index, item.pid)">
									<u-icon :name="item.commentDown" :color="item.commentDownColor" size="40"></u-icon>
									<view>{{item.commentDownCount}}</view>
								</view>
							</view>
						</view>
					</view>
				</view>
				<view v-if="index % 2 ==0">
					<view style="border-bottom: 2px solid #BA55D3; margin-right: 25rpx; margin-left: 400rpx;"></view>
				</view>
				<view v-else>
					<view style="border-bottom: 2px solid #1E90FF; margin-right: 25rpx; margin-left: 400rpx;"></view>
				</view>
			</view>
			<!-- 加载更多-->
			<u-loadmore :status="status" :icon-type="iconType" :load-text="loadText" font-size = 29 margin-top = 15 margin-bottom = 110 color="#1E90FF" icon-color="#1e90ff"  />
		</view>
		
		<!-- 全部评论模板 -->
		<view v-if="current == 2">
			<view v-for="(item,index) in commentList2">
				<view class="details" v-show="item.delShow">
					<view class="details_left">
						<view class="avatar">
							<u-avatar :src="item.avatarSrc" mode="square" size="mini"></u-avatar>
						</view>
					</view>
					<view class="details_right">
						<view class="details_right_top">
							<span>{{item.nickname}}</span><br>
							<span>{{item.commentDate}}</span>
						</view>
						<view class="del" v-show="item.commentShow" @click="delcomment(index,item.pid)">删除</view>
						<p>{{item.commentText}}</p>
						<view class="details_right_bottom">
							<view class="details_right_bottom_left" @click="reply(item.pid)">{{item.reCount}}回复 ></view>
							<view class="details_right_bottom_right">
								<view class="details_right_bottom_right_up" @click="commentUpFun(index, item.pid)">
									<u-icon :name="item.commentUp" :color="item.commentUpColor" size="40"></u-icon>
									<view>{{item.commentUpCount}}</view>
								</view>
								<view class="details_right_bottom_right_down" @click="commentDownFun(index, item.pid)">
									<u-icon :name="item.commentDown" :color="item.commentDownColor" size="40"></u-icon>
									<view>{{item.commentDownCount}}</view>
								</view>
							</view>
						</view>
					</view>
				</view>
				<view v-if="index % 2 ==0">
					<view style="border-bottom: 2px solid #BA55D3; margin-right: 25rpx; margin-left: 400rpx;"></view>
				</view>
				<view v-else>
					<view style="border-bottom: 2px solid #1E90FF; margin-right: 25rpx; margin-left: 400rpx;"></view>
				</view>
			</view>
			<!-- 加载更多-->
			<u-loadmore :status="status" :icon-type="iconType" :load-text="loadText" font-size = 29 margin-top = 15 margin-bottom = 110 color="#1E90FF" icon-color="#1e90ff"  />
		</view>
		
		<!-- 底部固定输入框 -->
		<view class="footer">
			<view class="avatar">
				<u-avatar :src="bottomAvatarSrc" mode="square" size="mini"></u-avatar>
			</view>
			<input v-model="form.text" type="text" placeholder="期待您的评论">
			<view class="button" @click="commentSubmit()">发送</view>
		</view>
		
	</view>
</template>

<script>
	export default{
		data(){
			return{
				status: 'loadmore',
				iconType: 'circle',
				loadText: {
					loadmore: '轻轻上拉加载更多...',
					loading: '努力加载中...',
					nomore: '别翻啦，我也是有底线的'
				},
				page0: 0,
				page1: 0,
				page2: 0,
				
				
				tabsList: [{
					name: '热门评论'
				}, {
					name: '最新评论'
				}, {
					name: '全部评论'
				}],
				current: 0,
				
				commentList0:[
					// {
					// 	avatarSrc:"",
					// 	nickname:"",
					// 	commentDate:"",
					// 	commentText:"",
					// 	reCount:"",
					// 	commentUp:'thumb-up',
					// 	commentUpColor:'#8a8a8a',
					// 	commentUpCount:'',
					// 	commentDown:'thumb-down',
					// 	commentDownColor:'#8a8a8a',
					// 	commentDownCount:'',
					// 	commentShow:false
					// }
				],
				commentList1:[
					// {
					// 	avatarSrc:"",
					// 	nickname:"七安",
					// 	commentDate:"2022-05-28 13:20:31",
					// 	commentText:"鼓浪屿的小街，就宽阔一些了，常常有墙里的植物，一大丛，绿的叶粉的花，从矮墙上垂下来，帘幕一般。建筑物上，看吧，常常看到一些盆栽，不知道是怎么地悬着放在窗台上，是那样地美化了它的街景。鼓浪屿的墙，有的嵌在一棵巨大的榕树干上，根条缠绕纠结，或者也不是榕树，是菩提树。树干巨大，仿佛整个岛屿都有了童话色彩了，像是一座古堡。　　比起南方的清幽与雅致，北方的墙仿佛更见沧桑。",
					// 	reCount:"50",
					// 	commentUp:'thumb-up',
					// 	commentUpColor:'#8a8a8a',
					// 	commentUpCount:'15',
					// 	commentDown:'thumb-down',
					// 	commentDownColor:'#8a8a8a',
					// 	commentDownCount:'1',
					// 	commentShow:false
					// }
				],
				commentList2:[
					// {
					// 	avatarSrc:"",
					// 	nickname:"七安吖",
					// 	commentDate:"2022-05-28 13:20:31",
					// 	commentText:"鼓浪屿的小街，就宽阔一些了，常常有墙里的植物，一大丛，绿的叶粉的花，从矮墙上垂下来，帘幕一般。建筑物上，看吧，常常看到一些盆栽，不知道是怎么地悬着放在窗台上，是那样地美化了它的街景。鼓浪屿的墙，有的嵌在一棵巨大的榕树干上，根条缠绕纠结，或者也不是榕树，是菩提树。树干巨大，仿佛整个岛屿都有了童话色彩了，像是一座古堡。　　比起南方的清幽与雅致，北方的墙仿佛更见沧桑。",
					// 	reCount:"50",
					// 	commentUp:'thumb-up',
					// 	commentUpColor:'#8a8a8a',
					// 	commentUpCount:'15',
					// 	commentDown:'thumb-down',
					// 	commentDownColor:'#8a8a8a',
					// 	commentDownCount:'1',
					// 	commentShow:false
					// }
				],
				bottomAvatarSrc:'',
				
				form:{
					text:''
				},
				
				rid:'',
				list:[],
				bgColor:{
				  "backgroundImage": "linear-gradient(45deg, #1E90FF, 	#BA55D3)"
				},
				title: "",
				date: "",
				readNumber: "",
				upCount:"",
				downCount:"",
				up:"thumb-up",
				down:"thumb-down",
				upColor:"#8a8a8a",
				downColor:"#8a8a8a",
				isUp:"",
				isDown:"",
				show: false,
				content: '请先登录，点击确认进行登录'
			}
		},
		onPageScroll(e) {
			// console.log("滚动距离为：" + e.scrollTop);
		},
		onShareAppMessage() {
			return {
			  title: 'TimeLaw每日推荐——' + this.title,
			  path: '/pages/index_recommend_details/index_recommend_details?rid=' + this.rid,
			  imageUrl: this.list[0].img
			}
			
		},
		onShareTimeline() {
			return {
			  title: 'TimeLaw每日推荐——' + this.title,
			  path: '/pages/index_recommend_details/index_recommend_details?rid=' + this.rid,
			  imageUrl: this.list[0].img
			}
		},
		onShow() {
			uni.request({
				url:'http://localhost/recommends/getOneRecommend/' + this.rid,
				method:"GET",
				header:{
					"token": uni.getStorageSync('token')
				},
				success: (e) => {
					// console.log(e.data.data);
					this.title = e.data.data.title;
					this.date = e.data.data.date;
					this.readNumber = e.data.data.readnumber;
					this.upCount = e.data.data.upcount;
					this.downCount = e.data.data.downcount;
					this.isUp = e.data.data.isUp;
					this.isDown = e. data.data.isDown;
					this.bottomAvatarSrc = e.data.data.avatar;
					
					//判断当前用户是否点赞----进行展示
					if(this.isUp === true){
						this.up = "thumb-up-fill";
						this.upColor = "#1E90FF";
					}
					if(this.isDown === true){
						this.down = "thumb-down-fill";
					}
				}
			});
			// 重新渲染点赞状态
			uni.request({
				// flag=0分页  flag=1全部
				url: 'http://localhost/recommends/getHotComment/' + this.rid +'/' + this.page0 +'/' + 1,
				method:"GET",
				header:{
					"token": uni.getStorageSync('token')
				},
				success: (e) => {
					console.log(e.data.data);
					var rearr = e.data.data;
					var arr = this.commentList0;
					for (var i = 0; i < arr.length; i++) {

						if(rearr[i].isUp === true){
							arr[i].commentUp = 'thumb-up-fill';
							arr[i].commentUpColor = '#1E90FF';
							arr[i].commentDown = 'thumb-down';
							arr[i].commentDownColor = '#8a8a8a';
						}
						if(rearr[i].isDown === true){
							arr[i].commentUp = 'thumb-up';
							arr[i].commentUpColor = '#8a8a8a';
							arr[i].commentDown = 'thumb-down-fill';
							arr[i].commentDownColor = '#8a8a8a';
						}
						if(rearr[i].isShow === true){
							arr[i].commentShow = true;
						}
					}
				}
			});
			// 重新渲染点赞状态
			uni.request({
				// flag=0分页  flag=1全部
				url: 'http://localhost/recommends/getNewComment/' + this.rid +'/' + this.page1 +'/' + 1,
				method:"GET",
				header:{
					"token": uni.getStorageSync('token')
				},
				success: (e) => {
					console.log(e.data.data);
					var rearr = e.data.data;
					var arr = this.commentList1;
					for (var i = 0; i < arr.length; i++) {
			
						if(rearr[i].isUp === true){
							arr[i].commentUp = 'thumb-up-fill';
							arr[i].commentUpColor = '#1E90FF';
							arr[i].commentDown = 'thumb-down';
							arr[i].commentDownColor = '#8a8a8a';
						}
						if(rearr[i].isDown === true){
							arr[i].commentUp = 'thumb-up';
							arr[i].commentUpColor = '#8a8a8a';
							arr[i].commentDown = 'thumb-down-fill';
							arr[i].commentDownColor = '#8a8a8a';
						}
						if(rearr[i].isShow === true){
							arr[i].commentShow = true;
						}
					}
				}
			});
			// 重新渲染点赞状态
			uni.request({
				// flag=0分页  flag=1全部
				url: 'http://localhost/recommends/getAllComment/' + this.rid +'/' + this.page2 +'/' + 1,
				method:"GET",
				header:{
					"token": uni.getStorageSync('token')
				},
				success: (e) => {
					console.log(e.data.data);
					var rearr = e.data.data;
					var arr = this.commentList2;
					for (var i = 0; i < arr.length; i++) {
			
						if(rearr[i].isUp === true){
							arr[i].commentUp = 'thumb-up-fill';
							arr[i].commentUpColor = '#1E90FF';
							arr[i].commentDown = 'thumb-down';
							arr[i].commentDownColor = '#8a8a8a';
						}
						if(rearr[i].isDown === true){
							arr[i].commentUp = 'thumb-up';
							arr[i].commentUpColor = '#8a8a8a';
							arr[i].commentDown = 'thumb-down-fill';
							arr[i].commentDownColor = '#8a8a8a';
						}
						if(rearr[i].isShow === true){
							arr[i].commentShow = true;
						}
					}
				}
			});
		},
		onLoad: function (option) {
			// console.log(option.rid);
			this.rid = option.rid;
			//获取信息
			uni.request({
				url:'http://localhost/recommends/getOneRecommend/' + this.rid,
				method:"GET",
				header:{
					"token": uni.getStorageSync('token')
				},
				success: (e) => {
					// console.log(e.data.data);
					this.title = e.data.data.title;
					this.date = e.data.data.date;
					this.readNumber = e.data.data.readnumber;
					this.upCount = e.data.data.upcount;
					this.downCount = e.data.data.downcount;
					this.isUp = e.data.data.isUp;
					this.isDown = e. data.data.isDown;
					this.bottomAvatarSrc = e.data.data.avatar;
					
					//判断当前用户是否点赞----进行展示
					if(this.isUp === true){
						this.up = "thumb-up-fill";
						this.upColor = "#1E90FF";
					}
					if(this.isDown === true){
						this.down = "thumb-down-fill";
					}
				}
			});
			//获取内容
			uni.request({
				url:'http://localhost/recommends/getOneRecommendDetails/' + this.rid,
				method:"GET",
				success: (e) => {
					// console.log(e.data.data);
					this.list = e.data.data;
					// console.log(this.list);
				}
			})
			//获取热门评论
			uni.request({
				url:'http://localhost/recommends/getHotComment/' + this.rid +'/' + this.page0 +'/' + 0,
				method:"GET",
				header:{
					"token": uni.getStorageSync('token')
				},
				success: (e) => {
					var arr = e.data.data
					// console.log(arr);
					for (var i = 0; i < arr.length; i++) {
						if(arr[i].isUp === true){
							//回写数据
							let obj = {
								avatarSrc: arr[i].avatar,
								nickname: arr[i].nickname,
								commentDate: arr[i].date,
								commentText: arr[i].text,
								reCount: arr[i].reCount,
								commentDownCount: arr[i].downcount,
								commentUpCount: arr[i].upcount,
								commentShow: arr[i].isShow,
								pid: arr[i].pid,
								delShow: true,
								commentDown:'thumb-down',
								commentDownColor:'#8a8a8a',
								commentUp:'thumb-up-fill',
								commentUpColor:'#1E90FF'
							}
							this.commentList0.push(obj);
						}
						else if(arr[i].isDown === true){
							let obj = {
								avatarSrc: arr[i].avatar,
								nickname: arr[i].nickname,
								commentDate: arr[i].date,
								commentText: arr[i].text,
								reCount: arr[i].reCount,
								commentDownCount: arr[i].downcount,
								commentUpCount: arr[i].upcount,
								commentShow: arr[i].isShow,
								pid: arr[i].pid,
								delShow: true,
								commentDown:'thumb-down-fill',
								commentDownColor:'#8a8a8a',
								commentUp:'thumb-up',
								commentUpColor:'#8a8a8a'
							}
							this.commentList0.push(obj);
						}
						else{
							let obj = {
								avatarSrc: arr[i].avatar,
								nickname: arr[i].nickname,
								commentDate: arr[i].date,
								commentText: arr[i].text,
								reCount: arr[i].reCount,
								commentDownCount: arr[i].downcount,
								commentUpCount: arr[i].upcount,
								commentShow: arr[i].isShow,
								pid: arr[i].pid,
								delShow: true,
								commentDown:'thumb-down',
								commentDownColor:'#8a8a8a',
								commentUp:'thumb-up',
								commentUpColor:'#8a8a8a',
							}			
							this.commentList0.push(obj);
						}						
					}
				}
			})
		},
		//触底
		onReachBottom() {
			this.status = 'loading';
			setTimeout(() => {
				if(this.current === 0){
					this.page0 = this.page0 + 5; 
					uni.request({
						url:'http://localhost/recommends/getHotComment/' + this.rid +'/' + this.page0 +'/' + 0,
						method:'GET',
						header:{
							"token": uni.getStorageSync('token')
						},
						success: (e) => {
