			detailsUp(){
				//校验用户是否登录
				var token = uni.getStorageSync('token');
				if(!token){
					this.show = true;
					return;
				}
				//点赞
				if(this.up === "thumb-up"){
					uni.request({
						url:'http://localhost/recommends/up/' + this.rid,
						method:"GET",
						header:{
