						success: (e) => {
							console.log(e);
							this.upCount = e.data.data.upcount;
							this.downCount = e.data.data.downcount;
							if(this.up === "thumb-up-fill"){
								this.up = "thumb-up"
								this.upColor = "#8a8a8a";
							}
							this.down = "thumb-down-fill"
							this.downColor = "#8a8a8a";
						}
					});
					return;
				}
				//取消踩
				if(this.down === "thumb-down-fill"){
					uni.request({
						url:'http://localhost/recommends/cancelDown/' + this.rid,
						method:"GET",
						header:{
							"token": uni.getStorageSync('token')
						},
						success: (e) => {
							console.log(e);
							this.upCount = e.data.data.upcount;
							this.downCount = e.data.data.downcount;
							this.down = "thumb-down"
							this.downColor = "#8a8a8a";
						}
					});
					return;
				}
			},
			
			//评论点赞
			commentUpFun(index, pid){
				console.log(pid);
				//校验用户是否登录
				var token = uni.getStorageSync('token');
				if(!token){
					this.show = true;
					return;
				}
				//点赞
				var arr;
				if(this.current === 0 ){
					arr = this.commentList0;
				}
				if(this.current === 1 ){
					arr = this.commentList1;
				}
				if(this.current === 2 ){
					arr = this.commentList2;
				}
				
				if(arr[index].commentUp === "thumb-up"){
					uni.request({
						url:'http://localhost/recommends/commentUp/' + pid,
						method:"GET",
						header:{
							"token": uni.getStorageSync('token')
						},
						success: (e) => {
							console.log(e);

							arr[index].commentUpCount = e.data.data.upcount;
							arr[index].commentDownCount = e.data.data.downcount;
							if(arr[index].commentDown === "thumb-down-fill"){
								arr[index].commentDown = "thumb-down"
								arr[index].commentDownColor = "#8a8a8a";
							}
							arr[index].commentUp = "thumb-up-fill"
							arr[index].commentUpColor = "#1E90FF";
						}
					});
					return;
				}
				
				//取消点赞
				if(arr[index].commentUp === "thumb-up-fill"){
					uni.request({
						url:'http://localhost/recommends/commentCancelUp/' + pid,
						method:"GET",
						header:{
							"token": uni.getStorageSync('token')
						},
