							console.log(e);
										
							arr[index].commentUpCount = e.data.data.upcount;
							arr[index].commentDownCount = e.data.data.downcount;
							arr[index].commentUp = "thumb-up"
							arr[index].commentUpColor = "#8a8a8a";
						}
					});
					return;
				}
			},
			
			//评论踩
			commentDownFun(index, pid){
				//校验用户是否登录
				var token = uni.getStorageSync('token');
				if(!token){
					this.show = true;
					return;
				}
				//踩
				var arr;
				if(this.current === 0 ){
					arr = this.commentList0;
				}
				if(this.current === 1 ){
					arr = this.commentList1;
				}
				if(this.current === 2 ){
					arr = this.commentList2;
				}
				
				if(arr[index].commentDown === "thumb-down"){
					uni.request({
						url:'http://localhost/recommends/commentDown/' + pid,
						method:"GET",
						header:{
							"token": uni.getStorageSync('token')
						},
						success: (e) => {
							console.log(e);
				
							arr[index].commentUpCount = e.data.data.upcount;
							arr[index].commentDownCount = e.data.data.downcount;
							if(arr[index].commentUp === "thumb-up-fill"){
								arr[index].commentUp = "thumb-up"
								arr[index].commentUpColor = "#8a8a8a";
							}
							arr[index].commentDown = "thumb-down-fill"
							arr[index].commentDownColor = "#8a8a8a";
						}
					});
					return;
				}
				
				//取消点赞
				if(arr[index].commentDown === "thumb-down-fill"){
					uni.request({
						url:'http://localhost/recommends/commentCancelDown/' + pid,
						method:"GET",
						header:{
							"token": uni.getStorageSync('token')
						},
						success: (e) => {
							console.log(e);
										
							arr[index].commentUpCount = e.data.data.upcount;
							arr[index].commentDownCount = e.data.data.downcount;
							arr[index].commentDown = "thumb-down"
							arr[index].commentDownColor = "#8a8a8a";
						}
					});
					return;
				}
			},
			//跳转评论详情
			reply(pid){
				uni.navigateTo({
					url:'../index_recommend_comment_details/index_recommend_comment_details?pid=' + pid
				})
			},
			//发布评论--到最新评论，动态渲染新数据
			commentSubmit(){
				var token = uni.getStorageSync('token');
				if(!token){
					this.show = true;
					return;
				}
				
				uni.request({
					url:'http://localhost/recommends/writeNewComment',
					method:"POST",
					header:{
						"token": uni.getStorageSync('token')
					},
					data:{
						"text": this.form.text,
						"rid": this.rid
					},
					success: (e) => {
						console.log(e);
						this.form.text = '';
						this.current = 1;
						// 重新渲染数据
						uni.request({
							url:'http://localhost/recommends/getNewComment/' + this.rid + '/0/2',
							method:"GET",
							header:{
								"token": uni.getStorageSync('token')
							},
							success: (e) => {
								var relist = e.data.data;
								let obj = {
									avatarSrc: relist[0].avatar,
									nickname: relist[0].nickname,
									commentDate: relist[0].date,
									commentText: relist[0].text,
									reCount: relist[0].reCount,
									commentDownCount: relist[0].downcount,
									commentUpCount: relist[0].upcount,
									commentShow: relist[0].isShow,
									pid: relist[0].pid,
									commentDown:'thumb-down',
									commentDownColor:'#8a8a8a',
									commentUp:'thumb-up',
									commentUpColor:'#8a8a8a'
								}
								var newlist = [];
								newlist.push(obj);
								var list = this.commentList1;
								//apply 劫持数组的 push 方法
								newlist.push.apply(newlist, list);
								this.commentList1 = newlist;
							}
						})
					}
				});
			},
			//删除
			delcomment(index, pid){
				console.log("index:",index);
				console.log("pid:",pid);
				var list;
				if(this.current === 0){
					list = this.commentList0;
				}
				if(this.current === 1){
					list = this.commentList1;
				}
				if(this.current === 2){
					list = this.commentList2;
				}
				
				uni.request({
					url:'http://localhost/recommends/delComment/' + pid,
					method:"GET",
					header:{
						"token": uni.getStorageSync('token')
					},
					success: (e) => {
						console.log(e);					
						// 删除原数组的索引元素-----------切换会自动同步最新数据
						list = list.splice(index,1);
					}
				});
			}
		}
	}
</script>

